
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
components: {
Tabbar,
TabbarItem
},
props: [],
data() {
return {
navigation: [
{
name: '活动管理',
link: '/activity'
},
{
name: '券码交易',
link: '/trade'
},
{
name: '我的',
link: '/mine'
}
]
}
},
computed: {},
methods: {
isSelected (tabbar) {
return this.$route.path.indexOf(tabbar) === 0
}
}
}



