import Vue from 'vue'
import Router from 'vue-router'
import Trade from '@/views/trade.vue'
import Buy from '@/views/trade/buy.vue'

// trade pages
const TradeList = () => import('@/views/trade/tradeList.vue')
const Sell = () => import('@/views/trade/sell.vue')
const Business = () => import('@/views/trade/business.vue')
const Information = () => import('@/views/trade/information.vue')

// activity pages
const Activity = () => import('@/views/activity.vue')
const ActivityList = () => import('@/views/activity/activityList.vue')
const AllAct = () => import('@/views/activity/all.vue')
const TodayAct = () => import('@/views/activity/today.vue')
const ConfigAct = () => import('@/views/activity/config.vue')
const StrategyAct = () => import('@/views/activity/strategy.vue')

// my pages
const Mine = () => import('@/views/mine.vue')

// import Activity from '@/views/activity.vue'
// import Mine from '@/views/mine.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/',
  routes: [
    {
      path: '/',
      redirect: '/trade/buy'
    },
    {
      path: '/activity',
      component: Activity,
      children: [
        {
          path: '',
          name: 'ActivityList',
          component: ActivityList
        },
        {
          path: 'all',
          name: 'All',
          component: AllAct
        },
        {
          path: 'today',
          name: 'Today',
          component: TodayAct
        },
        {
          path: 'config',
          name: 'Config',
          component: ConfigAct
        },
        {
          path: 'strategy',
          name: 'Strategy',
          component: StrategyAct
        }
      ]
    },
    {
      path: '/trade',
      component: Trade,
      children: [
        {
          path: '',
          name: 'TradeList',
          component: TradeList
        },
        {
          path: 'buy',
          name: 'Buy',
          component: Buy
        },
        {
          path: 'sell',
          name: 'Sell',
          component: Sell
        },
        {
          path: 'business',
          name: 'Business',
          component: Business
        },
        {
          path: 'information',
          name: 'Information',
          component: Information
        }
      ]
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    }
  ]
})



